<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>种植信息管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents('sys:plainfo:add')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents('sys:plainfo:modify')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents('sys:planinfo:delete')">删除</button>
                <button type="button" class="btn btn-warning" @click="stopPlant" v-if="$api.opreate.hasLoginComponents('sys:planinfo:stopPlant')">结束种植</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents('sys:plainfo:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                            <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                        </div>
                    </div>
                    <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
                    <div v-show="inforadmin">
                        <form class="form form-horizontal col-sm-8" ref="refForm">
                            <div class="form-group">
                                <label class="control-label col-sm-2">大棚编号</label>
                                <div class="col-sm-6">
                                    <select type="text" name="greenhouse" class="form-control" v-model="addData.greenhouse_id">
                                <option :value="item.deployNodeId" v-for="(item,index) of greenhouseid" :key="index" v-text="item.deployNodeId"></option>
                                 </select>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="control-label col-sm-2">作物名称</label>
                                <div class="col-sm-6">
                                    <select type="text" name="cropInfo" class="form-control" v-model="addData.crop_id">
                                <option :value="item.cropId" v-for="(item,index) of cropInfo" :key="index" v-text="item.cropName"></option>
                                 </select>
                                </div>
                            </div>

                            <div class="form-group">
                                <label class="control-label col-sm-2">备注</label>
                                <div class="col-sm-6" :class="{'has-success':!errors.has('remark')&&addData.remark!='','has-error':errors.has('remark')}">
                                    <textarea rows="6" class="form-control" name="remark" :placeholder="'请输入备注'" v-model="addData.remark" :data-vv-as="'备注'" v-validate="'required'"></textarea>
                                </div>
                                <div class="text-danger">
                                    <span v-show="errors.has('remark')">{{errors.first("remark")}}</span>
                                </div>
                            </div>
                            <div class="form-group col-sm-12">
                                <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                                <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
</template>

种植信息管理

<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "agriculture/selectCropPlantInfo.action",
                pageloginData: {
                    id: this.stateid
                },
                filterName: [],

            },
            greenhouseid: [],
            cropInfo: [],
            addData: {
                greenhouse_id: "",
                crop__id: "",
                remark: ""
            }
        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
    mounted() {
        this.$store.commit("UPDATETABLESELECTDATA", this.tableSelectData);
    },
    methods: {
        //大棚信息获取
        getgreenhouse_id() {
            var vm = this;
            vm.$axios
                .post(vm.HOST + "agriculture/selectDeployNodeInfo.action")
                .then(function (data) {
                    if (data.bool) {
                        vm.greenhouseid = data.data;
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        //获取作物ID
        getcropInfo() {
            var vm = this;
            vm.$axios
                .post(vm.HOST + "agriculture/selectCropInfo.action")
                .then(function (data) {
                    if (data.bool) {
                        vm.cropInfo = data.data;
                    }
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        stopPlant() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck("结束种植")) { //判断当前选中数据是否只有一条
            var datas={
                    id: vm.checkData[0].crop_plant_id
                };
                vm.$api.http.opreateHttp("", datas, "添加");
            }
        },
        //添加按钮按下
        inforAddClick(bool) {
            var vm = this;
            var opreate = this.opreate;
            if (bool) {
                vm.publicAddAndUpdate();
            }
            //修改状态下
            // if(opreate=='update'){
            //     this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]); //将选择中行的数据复制给addData，让form表单数据不为空
            // }
            // 异步函数，防止表单出现后存在错误，更改用户体验
            async function Empty() { //将addData数据修改为空
                for (var key in vm.addData) {
                    vm.addData[key] = "";
                }
                return true;
            }
            Empty().then(() => {
                vm.opreate = "add"; //将当前操作改为增加
                vm.inforadmin = bool; // 更改显示，根据bool确定
                vm.$validator.errors.clear(); //将表单错误数组重置为0
            })
        },
        //添加修改公共函数,添加修改界面初始化均需要执行获取数据或做其他处理的函数可以放入其中
        publicAddAndUpdate() {
            var vm = this;
            vm.getgreenhouse_id();
            vm.getcropInfo();
        },
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck("修改")) { //判断当前选中数据是否只有一条
                this.inforadmin = true; //更改显示，
                this.opreate = "update"; //将当前操作更改为修改
                vm.publicAddAndUpdate();
                this.addData = this.$api.opreate.ObjectCopy(this.checkData[0]); //将选择中行的数据复制给addData，让form表单数据不为空
            }
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData; // form表单的绑定数据
            var datas = { // 向数据接口传输的数据
                data: data,
                states: {
                    id: stateId
                }
            };
            // 异步修改操作 
            async function addDataFun() {
                var bool;
                if (vm.opreate == "update") {
                    bool = vm.$api.http.opreateHttp("agriculture/updateCropPlantInfo.action", datas, "修改");
                } else if (vm.opreate == "add") {
                    //添加操作
                    bool = vm.$api.http.opreateHttp("agriculture/addCropPlantInfo.action", datas, "添加");
                }
                return bool;
            }
            addDataFun().then(function (success) {
                if (success) {
                    vm.inforAddClick(false);

                }
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            vm.$validator.errors.clear(); //将表单错误数组重置为0
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA; // 获取当前页面的
            if (vm.$api.opreate.TESTcheck("删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].crop_plant_id, //需要删除行的数据id，需要根据行id进行相应修改 如选中行数据id为 userid,则此处需要改为checkData[0].userid
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            "agriculture/deleteCropPlantInfo.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入场景类型名后搜索", {
                    icon: 0
                });
            }
        }
    }
};
</script>

<style>

</style>



